:root {
    --text-color: #2D3132;
    --filtr-color: #444748;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

body {
    font-family: 'Stolzl';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--text-color);
}

img{
    width: 100%;
    display: block;
    object-fit: cover;
}

.container {
    max-width: 1320px;
    width: 100%;
    margin: 0 auto;
}
