@import url(./main.scss);
@import url(./range-input.css);
/*HEADER*/
.header {
    max-width: 1728px;
    width: 100%;
    margin: 0 auto;

    &__action {
        display: flex;
        align-items: center;
        justify-content: center;
        background: #CBDCFF;
    }

    &__action-img {
        width: 32px;
        height: 33px;
    }

    &__action-title {
        font-size: 16px;
        color: #0F1A34;
    }

    &__box {
        display: flex;
        align-items: center;
        padding: 20px 0;
        border-bottom: 1px solid #E0E3E3;
    }

    &__box-item {
        &:nth-child(1) {
            width: 15%;
        }

        &:nth-child(2) {
            width: 50%;
        }

        &:nth-child(3) {
            width: 35%;
        }
    }
}

.logo {
    width: 149.94px;
}

.search {
    width: 100%;
    border: 1.6px solid #E0E3E3;
    border-radius: 4px;
    padding: 11px 0 11px 52px;
    background-image: url(../img/search.svg);
    background-repeat: no-repeat;
    background-position: 16px 9px;

    &::placeholder {
        font-family: 'Stolzl';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 150%;
        color: #5C5F60;
        padding: 11px 0 11px 0px;
    }
}

.basket {
    display: flex;
    align-items: center;

    &__btn {
        background: #2D3132;
        border-radius: 4px;
        font-size: 12px;
        color: #FFFFFF;
        padding: 12px 22px;
        margin-left: 24px;
        font-family: 'Stolzl';
        font-style: normal;
        font-weight: 400;
        cursor: pointer;
    }

    &__lang {
        font-family: 'Stolzl';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 150%;
        color: #2D3132;
        border: none;
        margin-left: 24px;
        margin-right: 34px;
    }

    &__box {
        position: relative;
        width: 44px;

        & span {
            position: absolute;
            top: -4px;
            right: 14px;
            font-size: 10px;
            width: 14px;
            height: 14px;
            background: #EFF1F1;
            border: 2.4px solid #FFFFFF;
            border-radius: 13px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    &__heart {
        width: 24px;
        height: 24px;
    }

    &__message {
        width: 24px;
        height: 24px;
    }

    &__cart {
        width: 24px;
        height: 24px;
    }

    &__user {
        width: 32px;
        margin-left: 13px;
    }

    &__arrow {
        width: 12px;
        margin-left: 8px;
    }
}

#bg-color {
    background-color: #FF5449;
    color: #fff;
}

.menu {
    display: flex;

    &__link {
        font-weight: 500;
        font-size: 13px;
        letter-spacing: 0.4px;
        text-transform: uppercase;
        text-decoration: none;
        color: var(--text-color);
        padding: 16px;
        display: inline-block;
        &-active{
            border-bottom: 2px solid #444748;
        }
    }
    &__mob-btn{
        width: 30px;
        display: none;
    }
}

.mobile-menu {
    &__list {
        width: 250px;
        position: absolute;
        background-color: #fff;
        z-index: 1100;
        border: 1px solid #eeeeee;
        left: -100%;
        transition: all 1.5s;
        &.show-menu{
            left: 0;
            transition: all 0.5s;
        }
    }
    &__item {
        padding: 10px;
    }
    &__link {
        font-weight: 500;
        font-size: 13px;
        color: var(--filter-color);    
        text-decoration: none;
        padding: 10px;
    }
}

/*END HEADER*/
main{
    border-top: 1px solid #E0E3E3;
}

.navigation {
    display: flex;
    margin-top: 32px;
    &__item {
        font-size: 12px;

        & span {
            color: #747778;
        }

        &:last-child {
            color: #747778;
        }
    }
}

.catalog {
    &__title {
        font-size: 28px;
        margin: 40px 0 32px 0;
        display: inline-block;
    }

    &__mob-btn{
        width: 30px;
        cursor: pointer;
        margin-bottom: -8px;
        display: none;
    }

    &__wrapper {
        display: flex;
        justify-content: space-between;
    }

    &__subtitle {
        font-size: 22px;
        color: #5C5F60;
            
    }

    &__sort-text {
        font-size: 12px;
        color: #5C5F60;    
    }

    &__sort-select {
        padding: 12px;    
        width: 170px;
        border-radius: 100px; 
        border: none;
        background-color: #EFF1F1;   
        &::placeholder{
            font-family: 'Stolzl';
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 150%;
            color: #2D3132;    
        }
    }

    &__box {
        display: flex;
        margin-top: 24px;
        padding-bottom: 112px;
    }

    &__box-item {
        &:first-child{
            width: 312px;
            transition: all 1.5s;
            &.show-menu{
                left: 0;
                transition: all 0.5s;
            }
        }
        
    }

    &__box-item-wrapper {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-left: 24px;
    }
}

.filter {
    width: 312px;
    &__item {
        padding: 16px 16px 16px 24px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: #EFF1F1;
        margin-top: 1px;
        cursor: pointer;
        & span{
            color: #747778;
        }
    }

    &__area {
        padding: 24px;
    }
}

.range {
    &__value {
        display: flex;
        justify-content: space-between;
    }

    &__value-item {
        width: 45%;
        border-bottom: 1px solid #C4C7C7;
    }

    &__value-title {
        font-size: 10px;
        color: #747778;    
    }

    &__value-num {
        font-size: 12px;   
    }
}

.gender {
    &__box {
        display: flex;
        margin-bottom: 12px;
        position: relative;
    }
    &__box-num{
        position: absolute;
        right: 5px;
    }
    &__box-checkbox{
        position: absolute;
        z-index: -1;
        opacity: 0;
        &+label{
            display: inline-flex;
            align-items: center;
            user-select: none;
            &::before {
                content: '';
                display: inline-block;
                width: 20px;
                height: 20px;
                flex-shrink: 0;
                flex-grow: 0;
                border: 2px solid #adb5bd;
                border-radius: 0.25em;
                margin-right: 0.5em;
                background-repeat: no-repeat;
                background-position: center center;
                background-size: 50% 50%;
            }    
        }
        &:checked+label::before {
            border-color: var(--text-color);
            background-color: var(--text-color);
            background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
        }
    }
}

.card {
    width: 32%;
    border: 1px solid #EFF1F1;
    border-radius: 4px;
    margin-bottom: 24px;
    padding: 24px;
    position: relative;

    &__heart {
        width: 20px;
        position: absolute;
        right: 24px;
    }

    &__img {
        width: 207px;
        margin: 0 auto;
    }

    &__top {
        font-size: 10px;
        color: #00382F; 
        width: 38px;
        height: 23px;
        background-color: #B4FFF0;
        border-radius: 4px; 
        display: flex;     
        justify-content: center; 
        position: absolute;
    }

    &__brand {
        margin-top: 30px;
        font-weight: 500;
        font-size: 10px;
        color: #5C5F60;    
    }

    &__box {
        display: flex;
        margin-top: 24px;
        justify-content: space-between;
    }

    &__item {
        &:last-child{
            text-align: right;
        }
    }

    &__price-old {
        font-size: 12px;
        text-decoration-line: line-through;
        color: #747778;    
    }

    &__price-new {
        font-weight: 500;
    }

    &__location {
        font-size: 12px;
        position: relative;
        display: flex;
        align-items: center;
        &::before{
            content: "";
            background-image: url('../img/location.svg');
            background-size: cover;
            width: 10px;
            height: 12px;
            display: block;
        }
    }

    &__sell-box {
        display: flex;
    }

    &__sell-img {
        width: 20px;
        margin-right: 10px;
    }

    &__marker {
        font-size: 10px;
        width: 56px;
        height: 23px;
        background: #E0E3E3;
        border-radius: 4px;   
        display: flex;
        justify-content: center; 
    }
}

.breadcrumb {
    margin-top: 56px;
    display: flex;
    justify-content: center;
    align-items: center;
    &__btn {
        font-size: 12px;
        color: #5C5F60;   
        padding: 7px 13px;
        border: 1px solid #C4C7C7;
        border-radius: 26px; 
        cursor: pointer;
        &:first-child{
            margin-right: 8px;
        }
        &:last-child{
            margin-left: 8px;
        }
    }

    &__list {
        display: flex;
        list-style: none;
        align-items:center;
    }

    &__item {
        width: 34px;
        height: 34px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #C4C7C7;
        border-radius: 50%;
        margin-left: 8px;
        font-size: 10px;
        color: #5C5F60;
        &:first-child{
            margin-left: 0;
        }
    }
    &__active{
        width: 40px;
        height: 40px;
        border: 1.6px solid #2D3132
    }
}
/*FOOTER*/
.footer {
    padding: 72px 204px 24px 204px;
    background-color: #EFF1F1;
    &__box {
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid #C4C7C7;
        padding-bottom: 72px;
    }
    &__item {
        width: 23%;
        display: flex;
        flex-direction: column;
    }
    &__link {
        text-decoration: none;
        font-family: 'Stolzl';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
        color: var(--text-color);  
        margin-bottom: 12px;  
        &:last-child{
            margin-bottom: 0;
        }
    }
    &__title {
        margin-bottom: 12px;
    }
    &__img {
        width: 145.89px;
    }
    &__copyright {
        font-size: 12px;
        margin-top: 38px;
        margin-bottom: 14px;
    }
}
  
/*END FOOTER*/



.hidden {
    display: none;
}



/*MEDIA*/
@media(max-width: 1400px){
    .container{
        padding: 0 10px;
    }
    .basket__lang {
        margin-left: 15px;
        margin-right: 10px;
    }
    .basket__btn {
        margin-left: 10px;
        padding: 12px 20px;
    }
    .footer {
        padding: 72px 100px 24px 100px;
        background-color: #EFF1F1;
    }
}

@media(max-width: 1200px){
    .header__box-item:nth-child(2) {
        width: 45%;
    }
    .header__box-item:nth-child(3) {
        width: 40%;
    }
    .catalog__box-item:first-child {
        width: 250px;
    }
    .filter {
        width: 250px;
    }
}

@media(max-width: 1050px){
    .header__box-item:nth-child(2) {
        width: 40%;
    }
    .header__box-item:nth-child(3) {
        width: 45%;
    }
    .menu__link{
        padding: 15px;
    }
}

@media(max-width: 1020px){
    .filter{
        background: rgb(255, 255, 255);
    }
    .catalog__box-item:first-child {
        position: absolute;
        left: -100%;
        z-index: 1000;
    }
    .catalog__box-item-wrapper {
        margin-left: 0;
    }
    .footer {
        padding: 72px 20px 24px 20px;
    }
    .catalog__mob-btn{
        display: inline;
    }
    .menu__link{
        padding: 12px;
    }
}

@media(max-width: 992px){
    .menu{
        display: none;
    }
    .header__box-item:nth-child(1) {
        width: 17%;
    }
    .header__box-item:nth-child(2) {
        width: 30%;
    }
    .header__box-item:nth-child(3) {
        width: 55%;
    }
    .basket {
        justify-content: space-between;
    }
    .logo {
        width: 100px;
    }
    .menu__mob-btn{
        display: block;
    }
    main {
        border-top: none;
    }
}

@media(max-width: 840px){
    .basket__btn {
        font-size: 10px;
        padding: 12px 5px;
    }
}

@media(max-width: 768px){
    .card {
        width: 49%;
    }
    .footer__box {
        flex-wrap: wrap;
    }
    .footer__item {
        width: 49%;
        margin-bottom: 30px;
    }
    .header__box{
        flex-wrap: wrap;
    }
    .header__box-item:nth-child(1) {
        width: 50%;
    }
    .header__box-item:nth-child(2) {
        width: 50%;
    }
    .header__box-item:nth-child(3) {
        width: 100%;
    }
    .logo {
        width: 150px;
    }
    .basket__btn {
        padding: 12px 15px;
    }
    .footer__box {
        padding-bottom: 20px;
    }
    .breadcrumb__item {
        width: 24px;
        height: 24px;
    }
    .breadcrumb__btn {
        padding: 3px 7px;
    }
}

@media(max-width: 576px){
    .card {
        width: 100%;
    }
    .mobile-menu__list{
        width: 100%;
        padding-left: 30px;
    }
    .mobile-menu__link {
        font-size: 18px;
        padding: 12px 25px;
    }
    .breadcrumb__item:nth-child(5),
    .breadcrumb__item:nth-child(6),
    .breadcrumb__item:nth-child(7),
    .breadcrumb__item:nth-child(8),
    .breadcrumb__item:nth-child(9){
        display: none;
    }
    .footer__box{
        flex-direction: column;
        align-items: center;
    }
    .footer__item {
        width: 50%;
    }
}

@media(max-width: 440px){
    .basket__btn {
        margin-left: 0;
        padding: 10px;
    }
    .search {
        padding: 11px 0 11px 38px;
    }
    .basket__lang {
        display: none;
    }
    .header__action-title {
        font-size: 14px;
    }
    .footer__link {
        font-size: 12px;
    }
}
/*MEDIA*/
